.background-1 {
  background-color: $slideColor1;
}

.background-2 {
  background-color: $slideColor2;
}

.background-3 {
  background-color: $slideColor3;
}

.background-4 {
  background-color: $slideColor4;
}

.background-5 {
  background-color: $slideColor5;
}

body {
  overflow-x: hidden;
  height: auto;
  min-height: 100%;
  background: #4d4d6d;
  color: $gray-lighter;
}

p {
  color: $gray-lighter;
}

img {
  max-width: 100%;
}

.primary-title {
  font-size: 40px;
}

.next-slide {
  display: inline-block;
  margin-top: 40px;
  font-size: 30px;
  cursor: pointer;
}

#fp-nav ul li a span, .fp-slidesNav ul li a span {
  background: rgba($gray-lighter, 0.3);
}

.profile {
  display: flex;
  flex-direction: row;
  flex: 1 1 300px;
  justify-content: center;

  .profile__photo {
    position: relative;
    max-width: 200px;
    box-shadow: -1px -1px 5px rgba(#000, 0.4);

    &:after {
      content: '';
      background-image: linear-gradient(to top, rgba($gray-darker, 0.4) 0%, rgba($gray-darker, 0.2) 30%, transparent 100%);
      @include position(absolute, 0 0 0 0);
    }
  }
  .profile__info {
    display: flex;
    align-items: center;
    max-width: 600px;
    padding: 0 20px;

    p {
      color: $gray-lighter;
      font-size: rem(25px);
      line-height: 1.5;
      text-indent: 25px;
    }
  }
}
.skills {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex: 1 1 300px;
  flex-wrap: wrap;
  max-width: 1170%;
  padding: 0 40px;

  .skill {
    position: relative;
    margin: 10px 20px;

    .progressbar-text {
      font-size: rem(12px);
    }
  }
}
.content {
  max-width: 900px;
  margin: 0 auto;
  padding: 0 20px;
  text-indent: 25px;


  @include media-breakpoint-down(xs) {}

  p {
    font-size: rem(20px);
    line-height: 1.5;
  }
}

.experience {
  h2 {
    margin-bottom: 30px;
  }
}


